/*Calendar design*/
.calendar {
    display: flex;
    flex-direction: column;
    border: 1px solid black;
    margin: 2rem;
    height: max-content;
    width: 80%;
}

.calendarTitle {
    border: 1px solid black;
    background-color: #6d6875;
    height: 5rem;
    color: #eff7f6;
}

.calendarBody {
    display: flex;
    flex-direction: row;
    width: 100%;
}

/*design for all calendar cells*/
.rowName,
.columnName,
.calendarCell {
    border: 1px solid black;
    width: 80.9%;
    height: 1rem;
    margin: 0;
    padding: 2rem;
    font-size: 1.5rem;
    font-weight: 600;
}

.calendarDay {
    width: 16%;
}

.calendarMorning,
.calendarLunch,
.calendarAfternoon {
    width: 28%;
}

.calendarDay {
    background-color: #eff7f6;
}

.calendarMorning {
    background-color: #b2f7ef;
}

.calendarLunch {
    background-color: #f2b5d4;
}

.calendarAfternoon {
    background-color: #f8e16c;
}

/*dropdown menu selection design*/
.ddMenu {
    width: 100%;
    height: fit-content;
    background-color: rgba(240, 248, 255, 0);
    border: none;
    font-family: 'Handlee', cursive;
    font-size: 1.2rem;
    text-align: center;
}