/*recipe item component design*/
.recipeItem {
    width: 20rem;
    height: 23rem;
    margin: 2rem;
    border: 2px solid #4b4850;
    border-radius: 2rem;
    background-color: #eff7f6;
}

.recipeItem:hover {
    cursor: pointer;
    background-color: #d7c1c6;
}

.recipeImage {
    width: 15rem;
    margin: 1rem 1rem 0;
}

.recipeTitle {
    text-align: center;
    margin: 1rem;
}

.recipeCardContainer {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-color: #4b4850d6;
    display: flex;
    align-items: center;
    justify-content: center;
}

/*recipe card component design*/
.recipeCard {
    width: 80rem;
    height: max-content;
    margin-top: 0;
    background-color: #eff7f6;
    border: 2px solid #4b4850;
    display: flex;
    flex-direction: column;
    font-family: 'Pangolin', cursive;
}

.backButton {
    width: 5rem;
    background-color: #ffb4a2;
    margin: 1rem 2rem;
    border-radius: 1rem;
    font-family: 'Pangolin', cursive;
    font-size: 1.25rem;
}

.backButton:hover {
    cursor: pointer;
    color: #eff7f6;
    background-color: #4b4850;
}

.title {
    font-size: 3rem;
    text-align: center;
    margin: 0;
}

.recipeCardBody {
    display: flex;
    flex-direction: row;
}

.imageContainer {
    /*border: 2px solid green;*/
    width: 40%;
    margin: 2rem 2rem 4rem 4rem;
}

.recipeCardImage {
    width: 26rem;
    margin: 0 3rem;
}

.description {
    width: 50%;
    margin: 2rem 0 4rem 0;
}


.links button {
    width: 45.25%;
    font-family: 'Pangolin', cursive;
    font-size: 1.5rem;
}

.ingredientsMenu,
.procedureMenu {
    color: #4b4850;
    background-color: #ffb4a2;
    text-shadow: 2px 2px 3px #eff7f6;
}

.recipeBody {
    width: 85%;
    height: max-content;
    border: 2px solid #4b4850;
    background-color: #ffe4d6;
    padding: 1rem;
    font-size: 1.2rem;
}